@font-face {
  font-family: 'LabGrotesque';
  src: local('LabGrotesque'),
    url('./LabGrotesque-Regular.ttf') format('truetype'),
    url('./LabGrotesque-Regular.eot?#iefix') format('embedded-opentype'),
    url('./LabGrotesque-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

/* Medium */

@font-face {
  font-family: 'LabGrotesque';
