    url('./LabGrotesque-Medium.ttf') format('truetype'),
    url('./LabGrotesque-Medium.eot?#iefix') format('embedded-opentype'),
    url('./LabGrotesque-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}
